@mixin adaptive($point) {
    @if $point == desktop {
      @media (min-width: 1651px) { @content ; }
    } @else if $point == laptop {
      @media (min-width: 1025px) and (max-width: 1650px) { @content ; }
    } @else if $point == wide-tablet {
      @media (min-width: 768px) and (max-width: 1024px) { @content ; }
    } @else if $point == tablet {
      @media (min-width: 481px) and (max-width: 767px) { @content ; }
    } @else if $point == mobile {
      @media (max-width: 480px) { @content ; }
    }
  }